<template>
  <div>
    <van-nav-bar
      title=""
    />
    <van-dialog v-model="showDialog" title="" show-cancel-button>
      <img width="300" height="300" :src="imageBase64">
    </van-dialog>
    <van-button type="primary" block @click="toast()">Toast</van-button>
    <p />
    <van-button type="primary" block @click="openOther()"></van-button>
    <p />
    <van-button type="primary" block @click="qrCodeScan()"></van-button>
    <p />
    <van-button type="primary" block @click="getDevice()"></van-button>
    <p />
    <van-button type="primary" block @click="closePage()"></van-button>
    <p />
    <van-button type="primary" block @click="getLocationInfo()"></van-button>
    <p />
    <van-button type="primary" block @click="putData()"></van-button>
    <p />
    <van-button type="primary" block @click="getData()"></van-button>
    <p />
    <van-button type="primary" block @click="containsKey()">key</van-button>
    <p />
    <van-button type="primary" block @click="remoKey()">key</van-button>
    <p />
    <van-button type="primary" block @click="cleanAll()"></van-button>
    <p />
    <van-button type="primary" block @click="openBrowser()"></van-button>
    <p />
    <van-button type="primary" block @click="openNewBrowser()"></van-button>
    <p />
    <van-button type="primary" block @click="encode()"></van-button>
    <p />
    <van-button type="primary" block @click="decode()"></van-button>
    <p />
    <van-button type="primary" block @click="sign()"></van-button>
    <p />
    <van-button type="primary" block @click="callPhone()"></van-button>
    <p />
    <van-button type="primary" block @click="sendMsg()"></van-button>
    <p />
    <van-button type="primary" block @click="openContacts()"></van-button>
    <p />
    <van-button type="primary" block @click="openContact()"></van-button>
    <p />
    <van-button type="primary" block @click="openWeChat()"></van-button>
    <p />
    <van-button type="primary" block @click="switchScreen()"></van-button>
    <p />
    <van-button type="primary" block @click="imageSelect()"></van-button>
    <p />
    <van-button type="primary" block @click="idCardScan()"></van-button>
    <p />
    <van-button type="primary" block @click="shareWeChat()"></van-button>
    <p />
    <van-button type="primary" block @click="shareWeChatTimeline()"></van-button>
    <p />
    <van-button type="primary" block @click="faceScan()"></van-button>
    <p />
    <van-button type="primary" block @click="openCamera()"></van-button>
    <p />
    <van-button type="primary" block @click="openAlbum()"></van-button>
    <p />
    <van-button type="primary" block @click="pushData()"></van-button>
    <p />
    <van-button type="primary" block @click="dictation()"></van-button>
    <p />
    <van-button type="primary" block @click="picPreview()"></van-button>
    <p />
    <van-button type="primary" block @click="getClearData()"></van-button>
    <p />
    <van-button type="primary" block @click="loginOperation()"></van-button>
    <p />
    <van-button type="primary" block @click="loginOperationOut()"></van-button>
    <p />
    <van-button type="primary" block @click="testNetImage()">TestNetImage</van-button>
    <p />
    <van-button type="primary" block @click="openUrl()"></van-button>
    <p />
    <van-uploader :after-read="afterRead" />
  </div>
</template>

<script>
import lrz from 'lrz'
import native from '@/utils/js-native-n22/src/native'
// import native from 'js-native-n22'

console.log(native)

export default {
  name: 'Home',
  props: {},
  data() {
    return {
      showDialog: false,
      imageBase64: ''
    }
  },
  created() {},
  mounted() {
    native.bridge.registerhandler('functionInJs', (data, responseCallback) => {
      alert(JSON.stringify(data))
      responseCallback('JS OK')
    })
  },
  methods: {
    /**
     * Lrz
     */
    loadImageFile(path, successCallback, errorCallback, alwaysCallback) {
      lrz(path, { quality: 1 })
        .then((rst) => {
          // 
          successCallback && successCallback(rst)
        })
        .catch((err) => {
          // 
          errorCallback && errorCallback(err)
        })
        .always(() => {
          alwaysCallback && alwaysCallback()
        })
    },
    afterRead(file) {
      // 
      this.showDialog = !this.showDialog
      this.imageBase64 = file.content
    },
    testNetImage() {
      this.loadImageFile('http://img6.16fan.com/attachments/wenzhang/201805/18/152660818127263ge.jpeg', (rst) => {
        this.showDialog = !this.showDialog
        this.imageBase64 = rst.base64
      }, err => {
        alert(err)
      })
    },
    toast() {
      native.toast({ text: '', duration: 0 }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    getDevice() {
      native.device((content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    closePage() {
      native.close((content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    qrCodeScan() {
      native.scanQRCode((content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    getLocationInfo() {
      native.location({
        needAddress: true,
        mockEnable: false,
        httpTimeOut: 20000 // 8000
      }, (content) => {
        alert(' : ' + content.latitude)
        alert(' : ' + content.longitude)
        alert(' : ' + content.address)
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    putData() {
      native.storage({
        mode: 1,
        key: 'name',
        value: 'zhangxin'
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    getData() {
      native.storage({
        mode: 2,
        key: 'name'
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    containsKey() {
      native.storage({
        mode: 3,
        key: 'name'
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    remoKey() {
      native.storage({
        mode: 4,
        key: 'name'
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    cleanAll() {
      native.storage({
        mode: 5
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    openBrowser() {
      native.openBrowser({
        mode: 1,
        url: 'http://xrkj.gitee.io/jsbridge-n22/#/'
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    openNewBrowser() {
      native.openBrowser({
        mode: 2,
        url: 'http://xrkj.gitee.io/jsbridge-n22/#/',
        isCustom: true,
        isShowClose: true,
        isShowBack: true,
        isShowShare: true,
        toolbar: {
          height: 44,
          color: '#0000CC'
        },
        title: {
          color: '#666633',
          staticText: 'Title'
        }
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    sign() {
      native.security({
        mode: 1,
        key: 'default',
        content: '1234567890'
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    encode() {
      native.security({
        mode: 2,
        key: 'default',
        content: '1234567890'
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    decode() {
      native.security({
        mode: 3,
        key: 'default',
        content: 'zWI1jkgPpYUys5c06MYEQQ=='
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    callPhone() {
      native.callPhone({
        phoneNumber: '15617883302',
        extensionNumber: ',1'
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    sendMsg() {
      native.sendMsg({
        phoneNumber: '15617883302',
        msgInfo: ''
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    openContacts() {
      native.openContacts((content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    openContact() {
      native.openContact((content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    openWeChat() {
      native.openWeChat((content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    switchScreen() {
      native.switchScreen((content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    imageSelect() {
      native.imageSelect({
        'limit': 2
      }, (content) => {
        this.loadImageFile(content.paths[0], (rst) => {
          this.showDialog = !this.showDialog
          this.imageBase64 = rst.base64
        }, err => {
          alert(err)
        })
      }, (error) => {
        alert(error)
      })
    },
    shareWeChat() {
      native.shareWeChat({
        platform: '1',
        webPageUrl: 'http://n22.online/',
        iconUrl: 'https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=2841648446,236398816&fm=26&gp=0.jpg',
        title: '',
        desc: ''
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    shareWeChatTimeline() {
      native.shareWeChat({
        platform: '2',
        webPageUrl: 'http://n22.online/',
        iconUrl: 'https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=2841648446,236398816&fm=26&gp=0.jpg',
        title: '',
        desc: ''
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    idCardScan() {
      native.idCardScan({
        'isVertical': true, // 
        'cardType': 1 // 1:2:
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    faceScan() {
      native.faceScan((content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    openCamera() {
      native.openCamera({
        openFlag: '1', // ,1.,2.
        isCompress: 1 // 1.,2.
      }, (content) => {
        alert(JSON.stringify(content))
        this.loadImageFile(content.paths[0], (rst) => {
          this.showDialog = !this.showDialog
          this.imageBase64 = rst.base64
        }, err => {
          alert(err)
        })
      }, (error) => {
        alert(error)
      })
    },
    openAlbum() {
      native.openCamera({
        openFlag: '2', // ,1.,2.
        isCompress: 2, // 1.,2.
        photoNum: 3 // 
      }, (content) => {
        alert(JSON.stringify(content))
        this.loadImageFile(content.paths[0], (rst) => {
          this.showDialog = !this.showDialog
          this.imageBase64 = rst.base64
        }, err => {
          alert(err)
        })
      }, (error) => {
        alert(error)
      })
    },
    dictation() {
      native.dictation((content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    pushData() {
      native.pushData({
        event: 'push_agent_data',
        data: {
          agentName: 'zhangxin',
          agentCode: '1100011010',
          orgCode: '0001'
        }
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    picPreview() {
      native.picPreview({
        images: [
          'http://img6.16fan.com/201510/11/005258wdngg6rv0tpn8z9z.jpg',
          'http://img6.16fan.com/201510/11/013553aj3kp9u6iuz6k9uj.jpg',
          'http://img6.16fan.com/201510/11/011753fnanichdca0wbhxc.jpg',
          'http://img6.16fan.com/201510/11/011819zbzbciir9ctn295o.jpg',
          'http://img6.16fan.com/201510/11/004847l7w568jc5n5wn385.jpg',
          'http://img6.16fan.com/201510/11/004906z0a0a0e0hs56ce0t.jpg',
          'http://img6.16fan.com/201510/11/004937pwttwjt0bgtoton7.jpg',
          'http://img6.16fan.com/201510/11/004946t38ybzt8bq8c838y.jpg',
          'http://img6.16fan.com/201510/11/004955d8ftz3t1sttt7ft7.jpg',
          'http://img6.16fan.com/201510/11/005027qy2g55yyglb59zdu.jpg',
          'http://img6.16fan.com/201510/11/005229bbtxkczcl0btmw8e.jpg',
          'http://img6.16fan.com/attachments/wenzhang/201805/18/152660818127263ge.jpeg',
          'http://img6.16fan.com/attachments/wenzhang/201805/18/152660818716180ge.jpeg'
        ]
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    getClearData() {
      native.storage({
        mode: 2,
        key: 'userInfo'
      }, (content) => {
        alert(JSON.stringify(content.value))
        try {
          // 
          // content.value  JSON.parse(json)
          const result = JSON.parse(content.value)
          alert(JSON.stringify(result.personsal))
          alert(JSON.stringify(result.personsal.agentName))
        } catch {
          console.log('valueJSON,')
        }
        // 
        if (content['object'] !== undefined) {
          alert(JSON.stringify(content.object.personsal))
          alert(JSON.stringify(content.object.personsal.agentName))
        }
      }, (error) => {
        alert(error)
      })
    },
    loginOperation() {
      native.loginOperation({
        loginFlag: '1'
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    loginOperationOut() {
      native.loginOperation({
        loginFlag: '2'
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    openOther() {
      native.openOther(new Date(), (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    },
    openUrl() {
      native.openUrl({
        url: 'https://mitphone.sunlife-everbright.com:8010/com.ifp.ipartner/proposaMsg?uuid=657c1962b5a44b9ebec1ea53b57abd6b'
      }, (content) => {
        alert(JSON.stringify(content))
      }, (error) => {
        alert(error)
      })
    }
  }
}
</script>

