var u = navigator.userAgent;

function setupWebViewJavascriptBridge(callback) {
  if (!/(iPhone|iPad|iPod|iOS)/i.test(u)) {
    if (window.WebViewJavascriptBridge) {
      console.info('WebViewJavascriptBridgeReady is Ready');
      callback(window.WebViewJavascriptBridge);
    } else {
      console.info('WebViewJavascriptBridgeReady Waiting to be ready');
      document.addEventListener('WebViewJavascriptBridgeReady', function () {
        console.info('listener WebViewJavascriptBridgeReady is finish'); // Java

        window.WebViewJavascriptBridge.init(function (message, responseCallback) {
          console.info(message);
          responseCallback('JS PONG');
        });
        callback(window.WebViewJavascriptBridge);
      }, false);
    }
  }
}

export default {
  /**
   * Java
   * @param {Java} name
   * @param {Java,JSON} data
   * @param {Java} callback
   */
  callhandler: function callhandler(name, data, _callback) {
    if (!/(iPhone|iPad|iPod|iOS)/i.test(u)) {
      console.log('bridge callhandler android >>> ', name, data);
      setupWebViewJavascriptBridge(function (bridge) {
        bridge.callHandler(name, data, function (data) {
          data = JSON.parse(data);

          _callback(data);
        });
      });
    } else {
      console.log('bridge callhandler ios >>> ', name, data);
      window.GDIJSBridge.call({
        method: name,
        data: data,
        callback: function callback(result) {
          result = JSON.parse(result);

          _callback(result);
        }
      });
    }
  },

  /**
   * JsJava
   * @param {Javajs} name
   * @param {JavaJava} callback
   */
  registerhandler: function registerhandler(name, callback) {
    console.log('bridge registerhandler >>> ', name);
    setupWebViewJavascriptBridge(function (bridge) {
      bridge.registerHandler(name, function (data, responseCallback) {
        callback(data, responseCallback);
      });
    });
  }
};